    color: #ff0;
}

.navbar-expand-lg .navbar-nav .nav-link {
    padding-right: 2rem;
}

/*Multiple-menu*/
.dropdown-submenu {
    position: relative;
}

.dropdown-submenu a::after {
    transform: rotate(-90deg);
    position: absolute;
    right: 6px;
    top: 0.9em;
}

.dropdown-submenu .dropdown-menu {
    top: 0;
    left: 100%;
    margin-left: 0.1rem;
    margin-right: 0.1rem;
}

/*End Multiple-menu*/
