/* Swiper */

.swiper {
    width: 100%;
    height: 100%;
}

.swiper-slide {
    text-align: center;
    font-size: 18px;
    background: #fff;
    display: flex;
    justify-content: center;
    align-items: center;
}

.swiper-slide img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.swiper {
    width: 100%;
    /* height: 300px; */
    margin-left: auto;
    margin-right: auto;
}

.swiper-slide {
    background-size: cover;
    background-position: center;
}

.mySwiper2 {
    height: 80%;
    width: 100%;
}

.mySwiper {
    height: 20%;
    box-sizing: border-box;
    padding: 10px 0;
}

.mySwiper .swiper-slide {
    width: 25%;
    height: 100%;
    opacity: 0.4;
}

.mySwiper .swiper-slide-thumb-active {
    opacity: 1;
}

.swiper-slide img {
    display: block;
    width: 100%;
    height: 100%;
    object-fit: cover;
}

.swiper-button-next,
.swiper-button-prev {
    color: #333;
}

/* End Swiper */

.navbar__cart-quantity {
    display: inline-flex;
    width: 16px;
    height: 16px;
    aspect-ratio: 1;
    border-radius: 100%;
    font-size: 12px;
    justify-content: center;
    align-items: center;
    background-color: #f00;
    color: #fff;
}

.quantity input.plus,
.quantity input.minus {
    height: 34px;
    width: 40px;
    border: 1px solid #e6e6e6;
    border-radius: 4px;
    color: #000000;
    cursor: pointer;
    background: #f4f4f4;
    font-size: 20px;
    line-height: 30px;
    transition: all 0.2s ease;
}

.quantity input.plus:hover,
.quantity input.minus:hover {
    background-color: #e7e7e7;
}

.quantity input.qty {
    border: 1px solid #ebebeb;
    border-radius: 0;
    height: 34px;
    display: inline;
    background: #f4f4f4;
    margin: 0 5px;
    vertical-align: top;

    -webkit-border-radius: 0;
    -o-border-radius: 0;
    -moz-border-radius: 0;
    text-align: center;
    position: relative;
    border-radius: 4px;
}

.quantity input.qty::before {
    content: "Qty";
    position: absolute;
    left: 10px;
}

.animated.infinite {
    animation-iteration-count: infinite;
    animation-duration: 2s;
}

.call-zalo {
    display: block;
    position: fixed;
    right: 33px;
    bottom: 110px;
    z-index: 266666600 !important;
}

.call-zalo img {
    width: 40px;
    height: 40px;
}

.call-now {
    display: block;
    position: fixed;
    right: 0;
    bottom: 0;
    z-index: 266666600 !important;
}

.mh-contact {
    background-color: transparent;
    cursor: pointer;
    height: 110px;
    visibility: visible;
    width: 110px;
}

.mh-alo-ph-circle {
    background-color: transparent;
    border: 2px solid #ff8114;
    border-radius: 100%;
    position: absolute;
    top: 15px;
    left: 16px;
    height: 85px;
    width: 84px;
    opacity: 0.5;
}

.mh-alo-ph-circle-fill {
    background-color: rgba(255, 129, 20, 0.5);
    border: 2px solid transparent;
    border-radius: 100%;
    height: 60px;
    left: 28px;
    opacity: 0.75 !important;
    position: absolute;
    top: 28px;
    transition: all 0.5s ease 0s;
    width: 60px;
}

.mh-img-circle {
    background: #ff8114;
    border: 2px solid transparent;
    border-radius: 100%;
    width: 40px;
    height: 40px;
    opacity: 0.7;
    position: absolute;
    top: 38px;
    left: 39px;
    transform-origin: 50% 50% 0;
    transition: all 0.2s ease-in-out 0s;
    color: #fff;
    align-items: center;
    display: flex;
    justify-content: center;
}

.grecaptcha-badge {
    visibility: hidden !important;
}

#checkout_complete {
    .block-warp {
        border: solid 1px #f5f5f5;
        box-shadow: 2px 2px 30px -9px rgba(0, 0, 0, 0.75);
    }

    .cart-detail {
        max-height: 0;
